import moment from 'moment';

export default {
  age(years, months, days) {
    return {
      'years': Number(years),
      'months': Number(months),
      'days': Number(days)
    }
  },
  compareAges(firstAge, secondAge) {
    if(firstAge.years > secondAge.years) {
      return 1
    } else if(firstAge.years < secondAge.years) {
      return -1
    } else {
      if(firstAge.months > secondAge.months) {
        return 1
      } else if (firstAge.months < secondAge.months) {
        return -1
      } else {
        return 0
      }
    }
  },
  birthDateFromAge(age) {
    let date = moment()
    date.subtract(age.years, 'y')
    date.subtract(age.months, 'M')
    date.subtract(age.days, 'd')
    return date.toDate()
  },
  dateAtAge(birthday, age) {
    let date = moment(birthday).clone()
    date.add(age.years, 'y')
    date.add(age.months, 'M')
    date.add(age.days, 'd')
    return date.toDate()
  },
  ageToDate(from, to) {
    let fromCopy = moment(from).clone()
    let toCopy = moment(to).clone()
    let years = toCopy.year() - fromCopy.year()
    let months = 0
    if (toCopy.month() >= fromCopy.month()) {
      months = toCopy.month() - fromCopy.month()
    } else {
      months = toCopy.month() + 1 + (12 - (fromCopy.month() + 1))
      years--
    }
    let days = 0
    if(toCopy.day() >= fromCopy.day()) {
      days = toCopy.day() - fromCopy.day()
    } else {
      days = toCopy.day() + (31 - fromCopy.days())
    }
    return this.age(years, months, days)
  },
  divideAge(age, order) {
    let years = Math.floor(age.years / order)
    let months = Math.floor(age.months / order)
    months += Math.floor(((age.years/order) - years) * 12)
    if (months >= 12) {
      years++
      months -= 12
    }
    if (days >= 30) {
      months++
      days -= 30
    } else if (days > 15) {
      months++
    }
    
    let days = Math.floor(age.days)
    return this.age(years, months, days)
  },
  multiplyAge(age, order) {
    let years = age.years * order
    let months = age.months * order
    let days = age.days * order
    if(days >= 30) {
      months += Math.floor(days / 30)
      days %= 30
    }
    if(months >= 12) {
      years += Math.floor(months/12)
      months %= 12
    }
    return this.age(years, months, days)
  },
  subtractAge(firstAge, secondAge) {
    let years = 0
    let months = 0
    let days = 0
    if(firstAge.months >= secondAge.months) {
      years = firstAge.years - secondAge.years
      months = firstAge.months - secondAge.months
    } else {
      years = firstAge.years - secondAge.years - 1
      months = 12 - secondAge.months + firstAge.months
    }
    if(firstAge.days >= secondAge.days) {
      days = firstAge.days - secondAge.days
    } else {
      days = firstAge.days + (30 - secondAge.days)
    }
    if (months >= 12) {
      years++
      months -= 12
    }
    return this.age(years, months, days)
  },
  divideDate(from, to, order) {
    let ageToDateDivided = this.divideAge(this.ageToDate(from, to), order)
    let fromCopy = moment(from).clone()
    return fromCopy.add(ageToDateDivided.years, 'y').add(ageToDateDivided.months, 'M').toDate()
  },
  ageAddition(firstAge, secondAge) {
    let years = firstAge.years + secondAge.years
    let months = firstAge.months + secondAge.months
    let days = firstAge.days + secondAge.days
    if (days > 30) {
      months++
      days = -30
    } else if (days > 15) {
      months++
    }
    
    if(months >= 12) {
      years ++
      months -= 12
    }
    return this.age(years, months, days)
  }
}
