<template lang="pug">
  section.timeline-item
    div.item(:class="itemClass(itemTimeline)")
      span.dot(v-if="itemTimeline.title !== ''" :style="getBackgroundColour(itemTimeline.color)")
      h3.date-item {{ getFormattedDate(itemTimeline) }}
      h4.title-item(v-html="itemTimeline.title")
      p.description-item(v-html="itemTimeline.description")
</template>

<script>
export default {
  name: 'TimelineItem',
  props: {
    itemTimeline: {
      type: Object,
      default: () => ({})
    },
    colorDots: {
      type: String,
      default: '#2da1bf'
    },
    dateLocale: {
      type: String,
      default: ''
    }
  },
  methods: {
    itemClass(timeLineItem) {
      if(timeLineItem.title === '') {
        return 'emptyItem'
      }
    },
    getBackgroundColour(color) {
      return color ? `background:${color}` : `background:${this.colorDots}`
    },
    getFormattedDate(item) {
      if(item.title === '') {
        return ''
      }
      const locale = this.dateLocale || navigator.language
      const nameMonth = item.from.toLocaleDateString(locale, { month: 'long' })
      if (item.showDayAndMonth) {
        const day = item.from.getDate()
        return `${day}. ${nameMonth}`
      }

      return nameMonth
    }
  }
}
</script>

<style lang="scss" scoped>
.timeline-item {
  .emptyItem {
    height: 7vh;
  }
  .item {
    border-left: 5px solid #ccd5db;
    padding: 20px 0 20px 15px;
    position: relative;
  }
  .date-item {
    margin: 0;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 2px;
  }
  .title-item {
    margin: 0;
    padding: 5px 0;
    font-size: 15px;
    font-weight: 500;
  }
  .description-item {
    font-weight: 100;
    margin: 0;
  }
  .dot {
    display: block;
    position: absolute;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    left: -10px;
    top: 26px;
  }
}
</style>
