<template lang="pug">
  div
    el-row
      el-col(:span="4")
        div
          el-select(v-model="locale", :placeholder="$t('language')", @change="updateLanguage")
            el-option(
              v-for="item in languageOptions"
              :key="item.value"
              :label="$t(item.label)"
              :value="item.value"
            )
      el-col(:span="1") &nbsp
      el-col(:span="4")
        div
          el-select(v-model="inputFormat", :placeholder="$t('input format')")
            el-option(
              v-for="item in inputFormatOptions"
              :key="item.value"
              :label="$t(item.label)"
              :value="item.value"
            )
    el-row
      el-col(:span="24")
        h1 {{ $t('trauma timelines')}}
    el-row(v-if="inputFormatDates")
      el-col(:span="8")
        span {{ $t('birth date') + '&nbsp' }}
        el-date-picker(v-model="birthdayInput", @change="fillAgeInputs")
      el-col(:span="8")
        span  {{ $t('separation date') + '&nbsp'}}
        el-date-picker(v-model="separationDateInput", @change="fillSeparationInputs")
      el-col(:span="8")
        span  {{ $t('diagnosis date') + '&nbsp' }}
        el-date-picker(v-model="diagnosisDateInput", @change="fillDiagnosisInputs")
    el-row(v-else)
      el-col(:span="2") &nbsp
      el-col.trauma-timeline-ages-col(:span="6")
        span.trauma-timeline-ages-span {{ $t('age') + '&nbsp' }}
        el-input.trauma-timeline-ages-input(v-model="actualAgeYears", type="number", clearable, min="0", :placeholder="$t('years')", @input="checkAgeYears('BIRTHDAY')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
        el-input.trauma-timeline-ages-input(v-model="actualAgeMonths", type="number", clearable, min="0", max="11", :placeholder="$t('monthsInput')", @input="checkAgeMonths('BIRTHDAY')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-col(:span="1") &nbsp
      el-col.trauma-timeline-ages-col(:span="6")
        span.trauma-timeline-ages-span {{ $t('separation') + '&nbsp' }}
        el-input.trauma-timeline-ages-input(v-model="separationAgeYears", type="number", clearable, min="0" :placeholder="$t('years')", @input="checkAgeYears('SEPARATION')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
        el-input.trauma-timeline-ages-input(v-model="separationAgeMonths", type="number",clearable min="0", max="11", :placeholder="$t('monthsInput')", @input="checkAgeMonths('SEPARATION')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-col(:span="1") &nbsp
      el-col.trauma-timeline-ages-col(:span="6")
        span.trauma-timeline-ages-span {{ $t('diagnosis') + '&nbsp' }}
        el-input.trauma-timeline-ages-input(v-model="diagnosisAgeYears" , type="number", clearable, min="0", :placeholder="$t('years')", @input="checkAgeYears('DIAGNOSIS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
        el-input.trauma-timeline-ages-input(v-model="diagnosisAgeMonths",type="number", clearable, min="0", max="11", :placeholder="$t('monthsInput')", @input="checkAgeMonths('DIAGNOSIS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-col(:span="2") &nbsp
    el-row &nbsp
    el-row &nbsp
    el-row
      el-col(:span="timelineSpan", v-for="timeline, index in timelines", :key="index")
        Timeline(
          :timeline-items="timeline"
          :message-when-no-items="messageWhenNoItems"
          :unique-year="true"
          :show-day-and-month="true"
          :dateLocale="locale"
          order="asc"
        )
    //- el-row &nbsp
    //- el-row
    //-     form(action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_blank")
    //-       input(type="hidden" name="cmd" value="_s-xclick")
    //-       input(type="hidden" name="hosted_button_id" value="2BHBD5GLENQZN")
    //-       input(type="image" src="https://www.paypalobjects.com/en_US/FR/i/btn/btn_donateCC_LG.gif" border="0" name="submit" title="PayPal - The safer, easier way to pay online!" alt="Donate with PayPal button")
</template>

<script>
import Timeline from '@/components/Timeline.vue'
import moment from 'moment'
import _ from 'lodash'
import timeMachine from '@/utils/time-machine'
import cookie from '@/utils/cookie'
import i18n from '@/i18n'

export default {
  name: 'TraumasTimelines',
  components: {
    Timeline
  },
  data () {
    return {
      locale: cookie.get('lang'),
      birthdayInput: null,
      separationDateInput: null,
      diagnosisDateInput: null,
      messageWhenNoItems: '',
      inputFormat: 'ages',
      inputFormatOptions: [
        {
          value: 'dates',
          label: 'dates' 
        },
        {
          value: 'ages',
          label: 'ages' 
        }
      ],
      languageOptions: [
        {
          value: 'en',
          label: 'english' 
        }, 
        {
          value: 'ru',
          label: 'russian' 
        }
      ],
      actualAgeYears: NaN,
      actualAgeMonths: NaN,
      separationAgeYears: NaN,
      separationAgeMonths: NaN,
      diagnosisAgeYears: NaN,
      diagnosisAgeMonths: NaN,
    };
  },
  computed: {
    birthday() {
      if(this.inputFormatDates) {
        return this.birthdayInput
      }
      return timeMachine.birthDateFromAge(timeMachine.age(this.actualAgeYears, this.actualAgeMonths, 0))
    },
    separationDate() {
      if(this.inputFormatDates) {
        return this.separationDateInput
      }
      return timeMachine.dateAtAge(this.birthday, timeMachine.age(this.separationAgeYears, this.separationAgeMonths, 0))
    },
    diagnosisDate() {
      if(this.inputFormatDates) {
        return this.diagnosisDateInput
      }
      return timeMachine.dateAtAge(this.birthday, timeMachine.age(this.diagnosisAgeYears, this.diagnosisAgeMonths, 0))
    },
    inputFormatDates() {
      return this.inputFormat === 'dates'
    },
    displayFirstTimeline () {
      return this.birthdayFilled && this.diagnosisDateFilled
    },
    displaySecondTimeline () {
      return this.birthdayFilled && this.diagnosisDateFilled && this.separationDateFilled
    },
    birthdayFilled () {
      if(this.inputFormatDates) {
        return !_.isNil(this.birthday)
      }
      return !Number.isNaN(this.actualAgeYears) && this.actualAgeYears !== ''
        && !Number.isNaN(this.actualAgeMonths) && this.actualAgeMonths !== ''
    },
    separationDateFilled () {
      if(this.inputFormatDates) {
        return !_.isNil(this.separationDate)
      }
      return !Number.isNaN(this.separationAgeYears) && this.separationAgeYears !== ''
        && !Number.isNaN(this.separationAgeMonths) && this.separationAgeMonths !== ''
    },
    diagnosisDateFilled () {
      if(this.inputFormatDates) {
        return !_.isNil(this.diagnosisDate)
      }
      return !Number.isNaN(this.diagnosisAgeYears) && this.diagnosisAgeYears !== ''
        && !Number.isNaN(this.diagnosisAgeMonths) && this.diagnosisAgeMonths !== ''
    },
    timelineSpan() {
      return 24 / this.timelines.length
    },
    ageToday() {
      if(this.inputFormatDates) {
        return timeMachine.ageToDate(this.birthday, moment().toDate())
      }
      return timeMachine.age(this.actualAgeYears, this.actualAgeMonths, 0)
    },
    ageAtSeparation () {
      if(this.inputFormatDates) {
        return timeMachine.ageToDate(this.birthday, this.separationDate)
      }
      return timeMachine.age(this.separationAgeYears, this.separationAgeMonths, 0)
    },
    ageAtDiagnosis () {
      if(this.inputFormatDates) {
        return timeMachine.ageToDate(this.birthday, this.diagnosisDate)
      }
      return timeMachine.age(this.diagnosisAgeYears, this.diagnosisAgeMonths, 0)
    },
    separtionDateDouble () {
      let separationDateCopy = moment(this.separationDate).clone()
      return separationDateCopy.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M')
    },
    ageAtSeparationDateDouble () {
      return timeMachine.multiplyAge(this.ageAtSeparation, 2)
    },
    ageAtHalfDiagnosisDate () {
      if(this.inputFormatDates) {
        return timeMachine.ageToDate(this.birthday, this.dateAtHalfDiagnosis)
      }
      return timeMachine.divideAge(this.ageAtDiagnosis, 2)
    },
    dateAtHalfDiagnosis () {
      return timeMachine.divideDate(this.birthday, this.diagnosisDate, 2)
    },
    dateAtQuarterDiagnosis () {
      return timeMachine.divideDate(this.birthday, this.diagnosisDate, 4)
    },
    ageAtQuarterDiagnosisDate () {
      return timeMachine.divideAge(this.ageAtDiagnosis, 4)
    },
    dateAtFifthDiagnosis () {
      return timeMachine.divideDate(this.birthday, this.diagnosisDate, 5)
    },
    ageAtFifthDiagnosisDate () {
      return timeMachine.divideAge(this.ageAtDiagnosis, 5)
    },
    dateAtEighthDiagnosis () {
      return timeMachine.divideDate(this.birthday, this.diagnosisDate, 8)
    },
    ageAtEighthDiagnosisDate () {
      return timeMachine.divideAge(this.ageAtDiagnosis, 8)
    },
    ageAtDiagMinusSep () {
      return timeMachine.subtractAge(this.ageAtDiagnosis, this.ageAtSeparation)
    },
    dateAtDiagMinusSep () {
      return moment(this.birthday).clone().add(this.ageAtDiagMinusSep.years, 'y').add(this.ageAtDiagMinusSep.months, 'M').toDate()
    },
    ageAtDiagPlusSep () {
      return timeMachine.ageAddition(this.ageAtDiagnosis, this.ageAtSeparation)
    },
    dateAtDiagPlusSep () {
      return moment(this.birthday).clone().add(this.ageAtDiagPlusSep.years, 'y').add(this.ageAtDiagPlusSep.months, 'M').toDate()
    },
    ageAtEightDiagInMonths() {
      let months = this.ageAtEighthDiagnosisDate.years
      return timeMachine.age(0, months, 0)
    },
    dateAtEightDiagInMonths() {
      return moment(this.birthday).clone().add(this.ageAtEightDiagInMonths, 'M').toDate()
    },
    beforePregnancyMonths() {
      let months = 9 - this.ageAtEightDiagInMonths.months
      return timeMachine.age(0, months, 0)
    },
    dateBeforePregnancyMonths() {
      return moment(this.birthday).clone().subtract(9, 'M').subtract(this.beforePregnancyMonths, 'M').toDate()
    },
    pregnancyMonths() {
      return timeMachine.age(0, this.ageAtEightDiagInMonths.months, 0)
    },
    dateDuringPregnancyMonths() {
      return moment(this.birthday).clone().subtract(this.pregnancyMonths, 'M').toDate()
    },
    timelines() {
      let timelines = []
      if(this.birthdayFilled && this.diagnosisDateFilled) {
        let timeline = {
            elements: [],
            lastElementDate: null
        }
        timeline.elements.push({
            from: this.dateBeforePregnancyMonths,
            title: i18n.t('before pregnancy'),
            showDayAndMonth: false,
            description: this.formatMonthsAproximationBeforePregnancy(this.beforePregnancyMonths)
          })
        timeline.elements.push({
            from: this.dateDuringPregnancyMonths,
            title: i18n.t('pregnancy'),
            showDayAndMonth: false,
            description: this.formatMonthsAproximation(this.pregnancyMonths)
          })
        timeline.elements.push({
            from: this.birthday,
            title: i18n.t('birthday'),
            showDayAndMonth: false,
            description: '&nbsp'
          })
        timeline.elements.push({
            from: this.dateAtEightDiagInMonths,
            title: i18n.t('diagnosis') + "/8 " + "months",
            showDayAndMonth: false,
            description: this.formatMonthsAproximation(this.ageAtEightDiagInMonths)
          })
        if(this.separationDateFilled) {
          timeline.elements.push({
              from: this.separationDate,
              title: i18n.t('separation'),
              showDayAndMonth: false,
              description: this.formatAge(this.ageAtSeparation)
            })
          timeline.lastElementDate = moment(this.separationDate)
        } else {
          // Only one timeline whithout separation date: Use max date as last element date to insert all the elements
          timeline.lastElementDate = moment(new Date(8640000000000000))
        }
        timelines.push(timeline)
      }
      
      if(this.separationDateFilled && this.birthdayFilled && this.diagnosisDateFilled) {
        let endDate = moment(this.separationDate)
        let ageAtSeparation = this.ageAtSeparation
        let index = 1
        while(endDate.isBefore(moment())) {
          let timeline = {
            elements: [],
            lastElementDate: null
          }
          timeline.elements.push(this.generateBlancElement(endDate.clone().add(1, 'd').toDate()))
          timeline.elements.push(this.generateBlancElement(endDate.clone().subtract(2, 'd').toDate()))
          timeline.elements.push(this.generateBlancElement(endDate.clone().subtract(1, 'd').toDate()))
          let seprationTitle = i18n.t('separation')
          if(index > 1) {
            seprationTitle += ' * ' + (index)
          }
          timeline.elements.push({
            from: endDate.toDate(),
            title: seprationTitle,
            showDayAndMonth: false,
            description: this.formatAge(ageAtSeparation)
          })
          endDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
          ageAtSeparation = timeMachine.ageAddition(ageAtSeparation, this.ageAtSeparation)
          timeline.lastElementDate = moment(endDate).clone()
          index ++
          timeline.elements.push({
            from: endDate.toDate(),
            title: i18n.t('separation') + ' * ' + (index),
            showDayAndMonth: false,
            description: this.formatAge(ageAtSeparation)
          })
          timelines.push(timeline)
        }
      }
      
      if(this.birthdayFilled && this.diagnosisDateFilled) {
        let eighthDiagnosisDate = moment(this.dateAtEighthDiagnosis)
        let ageAtEightDiagnosis = this.ageAtEighthDiagnosisDate
        let element = {
            from: eighthDiagnosisDate.toDate(),
            title: i18n.t('diagnosis') + '/8',
            showDayAndMonth: false,
            description: this.formatAge(ageAtEightDiagnosis)
          }
        timelines[0].elements.push(element)
        let fifthDiagnosisDate = moment(this.dateAtFifthDiagnosis)
        let ageAtFifthDiagnosis = this.ageAtFifthDiagnosisDate
        element = {
            from: fifthDiagnosisDate.toDate(),
            title: i18n.t('diagnosis') + '/5 . ' + i18n.t('renaud'),
            showDayAndMonth: false,
            description: this.formatAge(ageAtFifthDiagnosis)
          }
        this.insertElemntToTimeLines(element, timelines)
        let quarterDiagnosisDate = moment(this.dateAtQuarterDiagnosis)
        let ageAtQuarterDiagnosis = this.ageAtQuarterDiagnosisDate
        element = {
            from: quarterDiagnosisDate.toDate(),
            title: i18n.t('diagnosis') + '/4',
            showDayAndMonth: false,
            description: this.formatAge(ageAtQuarterDiagnosis)
          }
        this.insertElemntToTimeLines(element, timelines)
        let halfDiagnosisDate = moment(this.dateAtHalfDiagnosis)
        let ageAtHalfDiagnosis = this.ageAtHalfDiagnosisDate
        element = {
            from: halfDiagnosisDate.toDate(),
            title: i18n.t('diagnosis') + '/2',
            showDayAndMonth: false,
            description: this.formatAge(ageAtHalfDiagnosis)
          }
        this.insertElemntToTimeLines(element, timelines)
        
        let diagnosisMultiplication = moment(this.diagnosisDate).clone().add(this.ageAtDiagnosis.years, 'y').add(this.ageAtDiagnosis.months, 'M').add(this.ageAtDiagnosis, 'd')
        let ageAtDiagnosisMultiplication = timeMachine.ageAddition(this.ageAtDiagnosis, this.ageAtDiagnosis)
        let index = 2
        while(diagnosisMultiplication.isBefore(moment())) {
          element = {
            from: diagnosisMultiplication.toDate(),
            title: i18n.t('diagnosis') + '*' + index,
            showDayAndMonth: false,
            description: this.formatAge(ageAtDiagnosisMultiplication)
          }
          this.insertElemntToTimeLines(element, timelines)
          index = Math.pow(index, 2)
          diagnosisMultiplication.add(index * this.ageAtDiagnosis.years, 'y').add(index * this.ageAtDiagnosis.months, 'M').add(index * this.ageAtDiagnosis, 'd')
          ageAtDiagnosisMultiplication = timeMachine.multiplyAge(this.ageAtDiagnosis, index)
        }
        
        if(this.separationDateFilled) {
          eighthDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
          ageAtEightDiagnosis = timeMachine.ageAddition(ageAtEightDiagnosis, this.ageAtSeparation)
          fifthDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
          ageAtFifthDiagnosis = timeMachine.ageAddition(ageAtFifthDiagnosis, this.ageAtSeparation)
          quarterDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
          ageAtQuarterDiagnosis = timeMachine.ageAddition(ageAtQuarterDiagnosis, this.ageAtSeparation)
          halfDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
          ageAtHalfDiagnosis = timeMachine.ageAddition(ageAtHalfDiagnosis, this.ageAtSeparation)
          let index = 1
          while(eighthDiagnosisDate.isBefore(moment())) {
            let elementTitle = i18n.t('diagnosis') + '/8 + ' + i18n.t('separation')
            if(index > 1) {
              elementTitle += '*' + index
            }
            let element = {
              from: eighthDiagnosisDate.toDate(),
              title: elementTitle,
              showDayAndMonth: false,
              description: this.formatAge(ageAtEightDiagnosis)
            }
            this.insertElemntToTimeLines(element, timelines)
            elementTitle = i18n.t('diagnosis') + '/5 + ' + i18n.t('separation')
            if(index > 1) {
              elementTitle += '*' + index
            }
            element = {
              from: fifthDiagnosisDate.toDate(),
              title: elementTitle,
              showDayAndMonth: false,
              description: this.formatAge(ageAtFifthDiagnosis)
            }
            this.insertElemntToTimeLines(element, timelines)
            elementTitle = i18n.t('diagnosis') + '/4 + ' + i18n.t('separation')
            if(index > 1) {
              elementTitle += '*' + index
            }
            element = {
              from: quarterDiagnosisDate.toDate(),
              title: elementTitle,
              showDayAndMonth: false,
              description: this.formatAge(ageAtQuarterDiagnosis)
            }
            this.insertElemntToTimeLines(element, timelines)
            elementTitle = i18n.t('diagnosis') + '/2 + ' + i18n.t('separation')
            if(index > 1) {
              elementTitle += '*' + index
            }
            element = {
              from: halfDiagnosisDate.toDate(),
              title: elementTitle,
              showDayAndMonth: false,
              description: this.formatAge(ageAtHalfDiagnosis)
            }
            this.insertElemntToTimeLines(element, timelines)
            eighthDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
            ageAtEightDiagnosis = timeMachine.ageAddition(ageAtEightDiagnosis, this.ageAtSeparation)
            fifthDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
            ageAtFifthDiagnosis = timeMachine.ageAddition(ageAtFifthDiagnosis, this.ageAtSeparation)
            quarterDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
            ageAtQuarterDiagnosis = timeMachine.ageAddition(ageAtQuarterDiagnosis, this.ageAtSeparation)
            halfDiagnosisDate.add(this.ageAtSeparation.years, 'y').add(this.ageAtSeparation.months, 'M').add(this.ageAtSeparation.days, 'd')
            ageAtHalfDiagnosis = timeMachine.ageAddition(ageAtHalfDiagnosis, this.ageAtSeparation)
            index++
          }
        }
      }
      if(this.birthdayFilled) {
        let element = {
          from: moment().toDate(),
          title: i18n.t('today'),
          showDayAndMonth: false,
          description: this.formatAge(this.ageToday)
        }
        this.insertElemntToTimeLines(element, timelines)
      }
      
      if(this.diagnosisDateFilled) {
        let element = {
          from: this.diagnosisDate,
          title: i18n.t('diagnosis'),
          showDayAndMonth: false,
          description: this.formatAge(this.ageAtDiagnosis)
        }
        this.insertElemntToTimeLines(element, timelines)
      }
      
      if(this.separationDateFilled && this.diagnosisDateFilled) {
        let element = {}
        if(moment(this.diagnosisDate).isAfter(moment(this.separationDate))) {
          element = {
            from: this.dateAtDiagMinusSep,
            title: i18n.t('diagnosis') + " - " + i18n.t('separation'),
            showDayAndMonth: false,
            description: this.formatAge(this.ageAtDiagMinusSep)
          }
        } else {
          element = {
            from: this.dateAtDiagPlusSep,
            title: i18n.t('diagnosis') + " + " + i18n.t('separation'),
            showDayAndMonth: false,
            description: this.formatAge(this.ageAtDiagPlusSep)
          }
        }
        this.insertElemntToTimeLines(element, timelines)
      }
        
      return _.map(timelines, timeline => {
        return timeline.elements
      })
    }
  },
  mounted () {
    this.$gtag.event('access', { method: 'Google' })
    if(cookie.get('lang') === 'ru' ) {
      let toTranslate = document.getElementsByTagName("h3")
      for (let index = 0; index < toTranslate.length; index++) {
        toTranslate[index].textContent = i18n.t(toTranslate[index].textContent)
      }
    }
  },
  methods: {
    updateLanguage () {
      if(this.$i18n.locale !== this.locale) {
        this.$i18n.locale = this.locale
        cookie.set('lang', this.locale)
      }
    },
    yearTranlation(years) {
     return (years >= 5 && years <= 20) ? i18n.t('years') : years.toString().split('').pop() == '1'
              ? i18n.t('oneYear'):(years.toString().split('').pop() == '2' || years.toString().split('').pop() == '3' || years.toString().split('').pop() == '4')
              ? i18n.t('yearsTwoToFour'):i18n.t('years')
    },
    monthsTranslation(months) {
      return months == 1 
              ? i18n.t('oneMonth'):(months == 2 || months == 3 || months == 4)
              ? i18n.t('monthsTwoToFour'):i18n.t('months')
    },
    formatAge(age) {
      let ageDescription = ''
      if(age.years > 0 || age.months > 0) {
        ageDescription = i18n.t('age') + ": "
      }
      if(age.years > 0) {
        ageDescription += age.years + ' '
          + this.yearTranlation(age.years)
      }
      if(age.months > 0) {
        if(age.years > 0) {
          ageDescription += ', '
        }
        ageDescription += age.months + ' '
          + this.monthsTranslation(age.months)
      }
      return ageDescription
    },
    formatMonthsAproximation(age) {
      let ageDescription = ''
      if(age.months > 0) {
        ageDescription += i18n.t('age') + ": "
          + age.months + ' - ' + (age.months + 1)
          + this.monthsTranslation(age.months)
      }
      return ageDescription
    },
    formatMonthsAproximationBeforePregnancy(age) {
      let ageDescription = ''
      if(age.months > 0) {
        ageDescription += age.months + ' - ' + (age.months - 1)
          + this.monthsTranslation(age.months)
      }
      return ageDescription
    },
    fillAgeInputs() {
      if(!_.isNil(this.birthdayInput)) {
        this.actualAgeYears = this.ageToday.years
        this.actualAgeMonths = this.ageToday.months
      }
    },
    fillSeparationInputs() {
      if(!_.isNil(this.birthdayInput) && !_.isNil(this.separationDateInput)) {
        this.separationAgeYears = this.ageAtSeparation.years
        this.separationAgeMonths = this.ageAtSeparation.months
      }
    },
    fillDiagnosisInputs() {
      if(!_.isNil(this.birthdayInput) && !_.isNil(this.diagnosisDateInput)) {
        this.diagnosisAgeYears = this.ageAtDiagnosis.years
        this.diagnosisAgeMonths = this.ageAtDiagnosis.months
      }
    },
    checkAgeYears(fieldName) {
      switch (fieldName) {
        case 'BIRTHDAY':
          if(Number(this.actualAgeYears) < 0) {
            this.actualAgeYears = 0
          }
          
          if(this.birthdayFilled) {
            this.birthdayInput = this.birthday
          }
          break;
        case 'SEPARATION':
          if(Number(this.separationAgeYears) < 0) {
            this.separationAgeYears = 0
          }
          
          if(this.separationDateFilled) {
            this.separationDateInput = this.separationDate
          }
          break;
        case 'DIAGNOSIS':
          if(Number(this.diagnosisAgeYears) < 0) {
            this.diagnosisAgeYears = 0
          }
          
          if(this.diagnosisDateFilled) {
            this.diagnosisDateInput = this.diagnosisDate
          }
          break;
        default:
          break;
      }
    },
    checkAgeMonths(fieldName) {
      switch (fieldName) {
        case 'BIRTHDAY':
          if(Number(this.actualAgeMonths) < 0) {
            this.actualAgeMonths = 0
          } else if (Number(this.actualAgeMonths) > 11) {
            this.actualAgeMonths = 11
          }
          
          if(this.birthdayFilled) {
            this.birthdayInput = this.birthday
          }
          break;
        case 'SEPARATION':
          if(Number(this.separationAgeMonths) < 0) {
            this.separationAgeMonths = 0
          } else if (Number(this.separationAgeMonths) > 11) {
            this.separationAgeMonths = 11
          }
          
          if(this.separationDateFilled) {
            this.separationDateInput = this.separationDate
          }
          break;
        case 'DIAGNOSIS':
          if(Number(this.diagnosisAgeMonths) < 0) {
            this.diagnosisAgeMonths = 0
          } else if (Number(this.diagnosisAgeMonths) > 11) {
            this.diagnosisAgeMonths = 11
          }
          
          if(this.diagnosisDateFilled) {
            this.diagnosisDateInput = this.diagnosisDate
          }
          break;
        default:
          break;
      }
    },
    insertElemntToTimeLines(element, timelines) {
      if(moment(element.from).isBefore(moment()) || moment(element.from).isSame(moment())) {
        for(let index = 0; index < timelines.length; index++) {
          let lastElementDate = timelines[index].lastElementDate
          if(moment(element.from).isBefore(lastElementDate)) {
            timelines[index].elements.push(element)
            return index
          }
        }
      }
    },
    generateBlancElement(from) {
      return {
          from: from,
          title: '',
          showDayAndMonth: false,
        }
    },
  },
}
</script>

